"use client";

import React, { useMemo, useState } from "react";
import TableDropdown from "@/components/common/TableDropdown";
import MemberModal, { MemberFormValue, MemberStatus } from "@/components/box/MemberModal";

export type Member = {
  id: string;
  name: string;
  email?: string;
  phone?: string;
  status: MemberStatus;
  membershipExpiresAt?: string; // "YYYY-MM-DD"
  notes?: string;
};

type Props = {
  initialMembers?: Member[];
};

const badgeClass = (status: MemberStatus) => {
  switch (status) {
    case "active":
      return "inline-flex items-center rounded-full bg-success-50 px-2.5 py-0.5 text-xs font-medium text-success-700 dark:bg-success-500/10 dark:text-success-400";
    case "expired":
      return "inline-flex items-center rounded-full bg-warning-50 px-2.5 py-0.5 text-xs font-medium text-warning-700 dark:bg-warning-500/10 dark:text-warning-400";
    case "inactive":
    default:
      return "inline-flex items-center rounded-full bg-gray-100 px-2.5 py-0.5 text-xs font-medium text-gray-700 dark:bg-white/[0.06] dark:text-gray-300";
  }
};

export default function MembersTable({ initialMembers }: Props) {
  const [query, setQuery] = useState("");
  const [members, setMembers] = useState<Member[]>(
    initialMembers ?? [
      {
        id: "m_1",
        name: "Juan Pérez",
        email: "juan@example.com",
        phone: "+34 600 111 222",
        status: "active",
        membershipExpiresAt: "2026-02-01",
        notes: "Hombro derecho (vigilar).",
      },
      {
        id: "m_2",
        name: "María López",
        email: "maria@example.com",
        phone: "+34 600 333 444",
        status: "expired",
        membershipExpiresAt: "2025-12-15",
      },
      {
        id: "m_3",
        name: "Pedro García",
        email: "pedro@example.com",
        phone: "+34 600 555 666",
        status: "inactive",
      },
    ]
  );

  // Modal state
  const [modalOpen, setModalOpen] = useState(false);
  const [modalMode, setModalMode] = useState<"create" | "edit">("create");
  const [editingMember, setEditingMember] = useState<Member | null>(null);

  const openCreate = () => {
    setModalMode("create");
    setEditingMember(null);
    setModalOpen(true);
  };

  const openEdit = (m: Member) => {
    setModalMode("edit");
    setEditingMember(m);
    setModalOpen(true);
  };

  const onSubmitModal = (val: MemberFormValue) => {
    if (modalMode === "create") {
      const newMember: Member = {
        id: `m_${Date.now()}`,
        name: val.name,
        email: val.email,
        phone: val.phone,
        status: val.status,
        membershipExpiresAt: val.membershipExpiresAt,
        notes: val.notes,
      };
      setMembers((prev) => [newMember, ...prev]);
      return;
    }

    if (modalMode === "edit" && editingMember) {
      setMembers((prev) =>
        prev.map((m) =>
          m.id === editingMember.id
            ? {
                ...m,
                name: val.name,
                email: val.email,
                phone: val.phone,
                status: val.status,
                membershipExpiresAt: val.membershipExpiresAt,
                notes: val.notes,
              }
            : m
        )
      );
    }
  };

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase();
    if (!q) return members;
    return members.filter((m) => {
      const haystack = `${m.name} ${m.email ?? ""} ${m.phone ?? ""} ${
        m.status
      }`.toLowerCase();
      return haystack.includes(q);
    });
  }, [members, query]);

  const onDeactivate = (id: string) => {
    setMembers((prev) =>
      prev.map((m) => (m.id === id ? { ...m, status: "inactive" } : m))
    );
  };

  const onDelete = (id: string) => {
    setMembers((prev) => prev.filter((m) => m.id !== id));
  };

  return (
    <div>
      {/* Modal */}
      <MemberModal
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
        mode={modalMode}
        initialValue={
          editingMember
            ? {
                id: editingMember.id,
                name: editingMember.name,
                email: editingMember.email,
                phone: editingMember.phone,
                status: editingMember.status,
                membershipExpiresAt: editingMember.membershipExpiresAt,
                notes: editingMember.notes,
              }
            : null
        }
        onSubmit={onSubmitModal}
      />

      {/* Top bar: search + add */}
      <div className="mb-4 flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between">
        <div className="relative w-full sm:max-w-[420px]">
          <span className="pointer-events-none absolute left-4 top-1/2 -translate-y-1/2">
            <svg
              className="fill-gray-500 dark:fill-gray-400"
              width="20"
              height="20"
              viewBox="0 0 20 20"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M3.04175 9.37363C3.04175 5.87693 5.87711 3.04199 9.37508 3.04199C12.8731 3.04199 15.7084 5.87693 15.7084 9.37363C15.7084 12.8703 12.8731 15.7053 9.37508 15.7053C5.87711 15.7053 3.04175 12.8703 3.04175 9.37363ZM9.37508 1.54199C5.04902 1.54199 1.54175 5.04817 1.54175 9.37363C1.54175 13.6991 5.04902 17.2053 9.37508 17.2053C11.2674 18.5309 17.9448 18.5309 18.2377 18.238C18.5306 17.9451 18.5306 17.4703 18.2377 17.1774L15.418 14.3573C16.5365 13.0033 17.2084 11.2669 17.2084 9.37363C17.2084 5.04817 13.7011 1.54199 9.37508 1.54199Z"
                fill=""
              />
            </svg>
          </span>

          <input
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            placeholder="Buscar miembro (nombre, email, teléfono)…"
            className="h-11 w-full rounded-lg border border-gray-200 bg-transparent py-2.5 pl-12 pr-4 text-sm text-gray-800 shadow-theme-xs placeholder:text-gray-400 focus:border-brand-300 focus:outline-hidden focus:ring-3 focus:ring-brand-500/10 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:placeholder:text-white/30 dark:focus:border-brand-800"
          />
        </div>

        <button
          type="button"
          className="inline-flex items-center justify-center gap-2 rounded-lg bg-brand-500 px-4 py-2.5 text-sm font-medium text-white shadow-theme-xs hover:bg-brand-600"
          onClick={openCreate}
        >
          <svg
            width="18"
            height="18"
            viewBox="0 0 18 18"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M9 3.75V14.25"
              stroke="currentColor"
              strokeWidth="1.5"
              strokeLinecap="round"
            />
            <path
              d="M3.75 9H14.25"
              stroke="currentColor"
              strokeWidth="1.5"
              strokeLinecap="round"
            />
          </svg>
          Añadir miembro
        </button>
      </div>

      {/* Table */}
      <div className="overflow-hidden rounded-xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-gray-900">
        <div className="max-w-full overflow-x-auto">
          <table className="w-full table-auto">
            <thead>
              <tr className="border-b border-gray-200 bg-gray-50 dark:border-gray-800 dark:bg-white/[0.03]">
                <th className="px-5 py-3 text-left text-xs font-medium uppercase tracking-wider text-gray-500 dark:text-gray-400">
                  Miembro
                </th>
                <th className="px-5 py-3 text-left text-xs font-medium uppercase tracking-wider text-gray-500 dark:text-gray-400">
                  Estado
                </th>
                <th className="px-5 py-3 text-left text-xs font-medium uppercase tracking-wider text-gray-500 dark:text-gray-400">
                  Vence
                </th>
                <th className="px-5 py-3 text-left text-xs font-medium uppercase tracking-wider text-gray-500 dark:text-gray-400">
                  Contacto
                </th>
                <th className="px-5 py-3 text-right text-xs font-medium uppercase tracking-wider text-gray-500 dark:text-gray-400">
                  Acciones
                </th>
              </tr>
            </thead>

            <tbody className="divide-y divide-gray-200 dark:divide-gray-800">
              {filtered.map((m) => (
                <tr key={m.id} className="hover:bg-gray-50 dark:hover:bg-white/[0.03]">
                  <td className="px-5 py-4">
                    <div className="font-medium text-gray-800 dark:text-white/90">
                      {m.name}
                    </div>
                    {m.notes ? (
                      <div className="mt-0.5 text-xs text-gray-500 dark:text-gray-400">
                        {m.notes}
                      </div>
                    ) : null}
                  </td>

                  <td className="px-5 py-4">
                    <span className={badgeClass(m.status)}>
                      {m.status === "active"
                        ? "Activo"
                        : m.status === "expired"
                        ? "Vencido"
                        : "Inactivo"}
                    </span>
                  </td>

                  <td className="px-5 py-4 text-sm text-gray-700 dark:text-gray-300">
                    {m.membershipExpiresAt ?? "—"}
                  </td>

                  <td className="px-5 py-4">
                    <div className="text-sm text-gray-700 dark:text-gray-300">
                      {m.email ?? "—"}
                    </div>
                    <div className="text-sm text-gray-500 dark:text-gray-400">
                      {m.phone ?? ""}
                    </div>
                  </td>

                  <td className="px-5 py-4">
                    <div className="flex justify-end">
                      <TableDropdown
                        items={[
                          {
                            label: "Editar",
                            onClick: () => openEdit(m),
                          },
                          {
                            label: "Desactivar",
                            onClick: () => onDeactivate(m.id),
                          },
                          {
                            label: "Eliminar",
                            onClick: () => onDelete(m.id),
                            isDanger: true,
                          },
                        ]}
                      />
                    </div>
                  </td>
                </tr>
              ))}

              {filtered.length === 0 ? (
                <tr>
                  <td
                    colSpan={5}
                    className="px-5 py-8 text-center text-sm text-gray-500 dark:text-gray-400"
                  >
                    No hay resultados para “{query}”.
                  </td>
                </tr>
              ) : null}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
