"use client";

import React, { useEffect, useMemo, useState } from "react";
import { Modal } from "@/components/ui/modal";

export type MemberStatus = "active" | "expired" | "inactive";

export type MemberFormValue = {
  id?: string;
  name: string;
  email?: string;
  phone?: string;
  status: MemberStatus;
  membershipExpiresAt?: string; // YYYY-MM-DD
  notes?: string;
};

type Props = {
  isOpen: boolean;
  onClose: () => void;
  mode: "create" | "edit";
  initialValue?: MemberFormValue | null;
  onSubmit: (value: MemberFormValue) => void;
};

const emptyValue: MemberFormValue = {
  name: "",
  email: "",
  phone: "",
  status: "active",
  membershipExpiresAt: "",
  notes: "",
};

export default function MemberModal({
  isOpen,
  onClose,
  mode,
  initialValue,
  onSubmit,
}: Props) {
  const title = mode === "create" ? "Añadir miembro" : "Editar miembro";

  const seed = useMemo(() => {
    if (mode === "edit" && initialValue) return initialValue;
    return emptyValue;
  }, [mode, initialValue]);

  const [value, setValue] = useState<MemberFormValue>(seed);
  const [errors, setErrors] = useState<{ name?: string }>({});

  useEffect(() => {
    if (isOpen) {
      setValue(seed);
      setErrors({});
    }
  }, [isOpen, seed]);

  const setField = <K extends keyof MemberFormValue>(
    key: K,
    v: MemberFormValue[K]
  ) => setValue((prev) => ({ ...prev, [key]: v }));

  const validate = () => {
    const next: { name?: string } = {};
    if (!value.name.trim()) next.name = "El nombre es obligatorio.";
    setErrors(next);
    return Object.keys(next).length === 0;
  };

  const handleSubmit = () => {
    if (!validate()) return;

    const cleaned: MemberFormValue = {
      ...value,
      name: value.name.trim(),
      email: value.email?.trim() || undefined,
      phone: value.phone?.trim() || undefined,
      membershipExpiresAt: value.membershipExpiresAt || undefined,
      notes: value.notes?.trim() || undefined,
    };

    onSubmit(cleaned);
    onClose();
  };

  return (
    <Modal isOpen={isOpen} onClose={onClose} className="max-w-[720px] p-6 sm:p-8">
      <div className="space-y-6">
        <div>
          <h2 className="text-xl font-semibold text-gray-800 dark:text-white/90">
            {title}
          </h2>
          <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
            Completa los datos básicos del miembro. (Más adelante conectamos esto a BD.)
          </p>
        </div>

        {/* Form */}
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2">
          {/* Nombre */}
          <div className="sm:col-span-2">
            <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
              Nombre *
            </label>
            <input
              value={value.name}
              onChange={(e) => setField("name", e.target.value)}
              className={`h-11 w-full rounded-lg border bg-white px-4 py-2.5 text-sm text-gray-800 shadow-theme-xs placeholder:text-gray-400 focus:border-brand-300 focus:outline-hidden focus:ring-3 focus:ring-brand-500/10 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:placeholder:text-white/30 dark:focus:border-brand-800 ${
                errors.name ? "border-danger-300 dark:border-danger-700" : "border-gray-200"
              }`}
              placeholder="Ej: Nicolás D’Agostino"
            />
            {errors.name ? (
              <p className="mt-1 text-xs text-danger-600 dark:text-danger-400">
                {errors.name}
              </p>
            ) : null}
          </div>

          {/* Email */}
          <div>
            <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
              Email
            </label>
            <input
              value={value.email ?? ""}
              onChange={(e) => setField("email", e.target.value)}
              className="h-11 w-full rounded-lg border border-gray-200 bg-white px-4 py-2.5 text-sm text-gray-800 shadow-theme-xs placeholder:text-gray-400 focus:border-brand-300 focus:outline-hidden focus:ring-3 focus:ring-brand-500/10 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:placeholder:text-white/30 dark:focus:border-brand-800"
              placeholder="ej: nico@box.com"
            />
          </div>

          {/* Teléfono */}
          <div>
            <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
              Teléfono
            </label>
            <input
              value={value.phone ?? ""}
              onChange={(e) => setField("phone", e.target.value)}
              className="h-11 w-full rounded-lg border border-gray-200 bg-white px-4 py-2.5 text-sm text-gray-800 shadow-theme-xs placeholder:text-gray-400 focus:border-brand-300 focus:outline-hidden focus:ring-3 focus:ring-brand-500/10 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:placeholder:text-white/30 dark:focus:border-brand-800"
              placeholder="+34 600 123 456"
            />
          </div>

          {/* Estado */}
          <div>
            <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
              Estado
            </label>
            <select
              value={value.status}
              onChange={(e) => setField("status", e.target.value as any)}
              className="h-11 w-full rounded-lg border border-gray-200 bg-white px-4 py-2.5 text-sm text-gray-800 shadow-theme-xs focus:border-brand-300 focus:outline-hidden focus:ring-3 focus:ring-brand-500/10 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:focus:border-brand-800"
            >
              <option value="active">Activo</option>
              <option value="expired">Vencido</option>
              <option value="inactive">Inactivo</option>
            </select>
          </div>

          {/* Vencimiento */}
          <div>
            <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
              Vencimiento (opcional)
            </label>
            <input
              type="date"
              value={value.membershipExpiresAt ?? ""}
              onChange={(e) => setField("membershipExpiresAt", e.target.value)}
              className="h-11 w-full rounded-lg border border-gray-200 bg-white px-4 py-2.5 text-sm text-gray-800 shadow-theme-xs focus:border-brand-300 focus:outline-hidden focus:ring-3 focus:ring-brand-500/10 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:focus:border-brand-800"
            />
          </div>

          {/* Notas */}
          <div className="sm:col-span-2">
            <label className="mb-2 block text-sm font-medium text-gray-700 dark:text-gray-300">
              Notas / lesiones (solo interno)
            </label>
            <textarea
              value={value.notes ?? ""}
              onChange={(e) => setField("notes", e.target.value)}
              rows={4}
              className="w-full rounded-lg border border-gray-200 bg-white px-4 py-3 text-sm text-gray-800 shadow-theme-xs placeholder:text-gray-400 focus:border-brand-300 focus:outline-hidden focus:ring-3 focus:ring-brand-500/10 dark:border-gray-800 dark:bg-white/[0.03] dark:text-white/90 dark:placeholder:text-white/30 dark:focus:border-brand-800"
              placeholder="Ej: Rodilla izquierda, evitar saltos altos. / Preferencia: clases mañana."
            />
          </div>
        </div>

        {/* Footer actions */}
        <div className="flex flex-col-reverse gap-3 sm:flex-row sm:justify-end">
          <button
            type="button"
            onClick={onClose}
            className="inline-flex items-center justify-center rounded-lg border border-gray-200 bg-white px-4 py-2.5 text-sm font-medium text-gray-700 shadow-theme-xs hover:bg-gray-50 dark:border-gray-800 dark:bg-white/[0.03] dark:text-gray-300 dark:hover:bg-white/[0.06]"
          >
            Cancelar
          </button>
          <button
            type="button"
            onClick={handleSubmit}
            className="inline-flex items-center justify-center rounded-lg bg-brand-500 px-4 py-2.5 text-sm font-medium text-white shadow-theme-xs hover:bg-brand-600"
          >
            Guardar
          </button>
        </div>
      </div>
    </Modal>
  );
}
